<template>
    <div class="banner">
        <van-swipe :autoplay="3000" indicator-color="white">
            <van-swipe-item>
                <div class="item">
                     <img class="item-img" src="https://cimgs1.fenqile.com/ibanner2/M00/32/7F/jqgHAFwHk1GAOjvaAAHVTZtgBys011_750x320.jpg" alt="">
                </div>
            </van-swipe-item>
             <van-swipe-item>
                <div class="item">
                     <img class="item-img" src="https://cimgs1.fenqile.com/ibanner2/M00/32/7E/jqgHAFwHNdWALvquAAEjnP3iyJw640_750x320.jpg" alt="">
                </div>
            </van-swipe-item>
             <van-swipe-item>
                <div class="item">
                     <img class="item-img" src="https://cimgs1.fenqile.com/ibanner2/M00/32/82/jqgHAFwI2_qAIZZdAAGoO_8-BGQ809_750x320.jpg" alt="">
                </div>
            </van-swipe-item>
             <van-swipe-item>
                <div class="item">
                     <img class="item-img" src="https://cimgs1.fenqile.com/ibanner2/M00/32/7D/jqgHAFwGPF-AOliqAAEhV7nTqoI095_750x320.jpg" alt="">
                </div>
            </van-swipe-item>
        </van-swipe>
    </div>
</template>

<script>

import { Swipe, SwipeItem } from 'vant'

export default {
    name : 'homeBanner',
    components:{
        [Swipe.name]:Swipe,
        [SwipeItem.name]:SwipeItem
    }
}

</script>

<style lang="less" scoped>
@import '~styles/theme.less';
.item{
    height: 3.2rem;
    background-color: antiquewhite
}
.item-img{
    width: 100%;
    height: auto;
}
</style>



