<template>
        <div :endTime="endTime" :endText="endText" class="count-down">
          <span class="time-tip">距离本场结束</span>
            <slot>
              <div v-html="content" class="time-content">
              </div>
            </slot>
        </div>
</template>

<script>
export default {
    name:'countDown',
    data(){
      return {
      content: ``,
      }
    },
    props:{
      endTime:{
        type: String,
        default :''
      },
      endText:{
        type : String,
        default:'已结束'
      }
    },
    mounted () {
      this.countinit(this.endTime)
      this.countdowm(this.endTime)
    },
    methods: {
      countinit (timestamp,timer=null) {
        let self = this;
        let nowTime = new Date();
        let endTime = new Date(timestamp * 1000);
        let t = endTime.getTime() - nowTime.getTime();
        if(t>0){
          let day = Math.floor(t/86400000);
          let hour=Math.floor((t/3600000)%24);
          let min=Math.floor((t/60000)%60);
          let sec=Math.floor((t/1000)%60);
          hour = hour < 10 ? "0" + hour : hour;
          min = min < 10 ? "0" + min : min;
          sec = sec < 10 ? "0" + sec : sec;
          let format;
          if(day > 0){
            format = `<span style="color:rgb(255, 68, 68);font-size:.28rem;">${day}天${hour}小时${min}分${sec}秒</span>`;
          } 
          if(day <= 0 && hour > 0 ){
            format = `<span style="color:rgb(255, 68, 68);font-size:.28rem;">${hour}小时${min}分${sec}秒</span>`; 
          }
          if(day <= 0 && hour <= 0){
            format =`<span style="color:rgb(255, 68, 68);font-size:.28rem;">${min}分${sec}秒</span>`;
          }
          self.content = format;
          }else{
           clearInterval(timer);
           self.content = self.endText;
          }
      },
      countdowm(timestamp){
      let self = this;
      let timer = setInterval(function(){
       self.countinit(timestamp,timer)
        },1000);
      }
    }
}
</script>

<style lang="less" scoped>
.time-tip{
    font-size: .26rem;
    color: #979dab;
    line-height: 20px;
    margin-right: 4px;
}
</style>



